import * as mutationTypes from "./mutation-types";

export default {
  [mutationTypes.BALANCE_FETCHED](state, response) {
    state.balances = response.data;
  },
  [mutationTypes.BALANCE_TABLE_LOADING](state, value) {
    state.balanceTableLoading = value;
  },
  [mutationTypes.WAYBILLS_FETCHED](state, waybills) {
    state.waybills = waybills;
  }
};
