<template>
  <v-card>
    <v-toolbar dark dense flat>
      <v-row align="center">
        <v-col>
          <div>{{`${details._id.waybillType === 'income' ? 'Приходная':'Расходная'}`}} накладная {{details._id.waybill_id}}</div>
        </v-col>
        <div class="flex-grow-1" />
        <v-col cols="auto">
          <div>{{details._id.date}}</div>
        </v-col>
        <v-divider dark vertical />
        <v-col cols="auto">
          <v-btn small icon @click="download(details)">
            <v-icon>mdi-printer</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-toolbar>
    <v-card-text>
      <v-row>
        <v-col>
          <div class="text-center">{{details._id.stock.title}}</div>
        </v-col>
        <v-divider vertical />
        <v-col>
          <div class="text-center">{{details._id.waybillSubType}}</div>
        </v-col>
      </v-row>
      <v-simple-table>
        <template v-slot:default>
          <thead>
            <tr>
              <th>№</th>
              <th>Категория</th>
              <th>Артикул</th>
              <th>Цена</th>
              <th>Количество</th>
              <th>Сумма</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in details.items" :key="`${index}-${item.product._id}`">
              <td>{{index + 1}}</td>
              <td>{{item.category.title}}</td>
              <td>{{item.product.title}}</td>
              <td>{{item.price}}</td>
              <td>{{item.quantity}} {{item.unit}}</td>
              <td>{{item.price * item.quantity}}</td>
            </tr>
            <tr>
              <td colspan="5">Итого</td>
              <td>
                <strong>{{totalSumm}}</strong>
              </td>
            </tr>
          </tbody>
          <tfoot></tfoot>
        </template>
      </v-simple-table>
    </v-card-text>
  </v-card>
</template>

<script>
import { saveAs } from "file-saver";
import api from "@/plugins/api";
export default {
  props: {
    details: Object
  },
  computed: {
    totalSumm() {
      return this.details.items.reduce(
        (acc, item) => acc + item.price * item.quantity,
        0
      );
    }
  },
  methods: {
    async download(waybill) {
      let { data } = await api({
        url: `/api/transactions/waybill/document/`,
        method: "POST",
        responseType: "blob",
        data: waybill
      });
      await saveAs(data, `Накладная-${waybill.date}.docx`);
    }
  }
};
</script>

