<template>
  <div>
    <v-card color="blue" tile flat>
      <v-card-text>
        <v-card-title class="white--text">Остатки</v-card-title>
      </v-card-text>
    </v-card>
    <v-container>
      <v-row justify="center">
        <v-col>
          <v-card>
            <v-toolbar :extended="showFilters" extension-height="96px" elevation="2">
              <v-row align="center">
                <v-col>
                  <v-text-field
                    label="Поиск по модели"
                    hide-details
                    flat
                    solo
                    prepend-icon="mdi-magnify"
                  />
                </v-col>
                <v-col cols="auto">
                  <v-btn text @click.stop="toggleFilters">
                    <v-icon left>{{showFilters ? 'mdi-filter-outline' : 'mdi-filter'}}</v-icon>Фильтры
                  </v-btn>
                </v-col>
              </v-row>
              <template v-if="showFilters" #extension>
                <v-row>
                  <v-col cols="auto">
                    <v-menu
                      v-model="startDateModal"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          v-model="startDate"
                          label="Остаток на начало"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker @input="fetchBalances()" v-model="startDate" scrollable></v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col cols="auto">
                    <v-menu
                      v-model="endDateModal"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          v-model="endDate"
                          label="Остаток на конец"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker @input="fetchBalances()" v-model="endDate" scrollable></v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col cols="auto">
                    <v-select
                      @change="fetchBalances()"
                      v-model="stock"
                      :items="stocks"
                      item-value="_id"
                      item-text="title"
                      label="Выберите склад"
                    ></v-select>
                  </v-col>
                </v-row>
              </template>
            </v-toolbar>
            <v-data-table :loading="balanceTableLoading" :headers="headers" :items="items">
              <template v-slot:body="{items}">
                <tbody>
                  <tr v-for="item in items" :key="item._id">
                    <td>{{item.product.title}}</td>
                    <td>{{item.category.title}}</td>
                    <td>{{item.product.price_retail}}</td>
                    <td>{{item.startBalance}} {{item.category.unit}}</td>
                    <td>{{item.totalIncome}} {{item.category.unit}}</td>
                    <td>{{item.totalOutcome}} {{item.category.unit}}</td>
                    <td>{{item.endBalance}} {{item.category.unit}}</td>
                    <td class="text-right">
                      <v-btn
                        target="_blank"
                        :href="`/balances/transactions?item=${item._id}&stock=${stock}&startDate=${startDate}&endDate=${endDate}`"
                        icon
                      >
                        <v-icon>mdi-eye</v-icon>
                      </v-btn>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-data-table>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { BALANCE_FETCH } from "@/store/transaction/action-types";
import moment from "moment";
import { mapState } from "vuex";
export default {
  computed: {
    stocks() {
      return this.$store.state.settings.contability.stocks;
    },
    items() {
      return this.$store.state.transaction.balances;
    },
    balanceTableLoading() {
      return this.$store.state.transaction.balanceTableLoading;
    }
  },

  methods: {
    toggleFilters() {
      this.showFilters = !this.showFilters;
    },
    fetchBalances() {
      this.endDateModal = false;
      this.startDateModal = false;
      if (this.startDate <= this.endDate && this.stock) {
        this.$store.dispatch(BALANCE_FETCH, {
          stock: this.stock,
          startDate: this.startDate,
          endDate: this.endDate
        });
      }
    }
  },
  data() {
    return {
      showFilters: true,
      startDate: moment()
        .subtract(1, "months")
        .format("YYYY-MM-DD"),
      endDate: moment().format("YYYY-MM-DD"),
      startDateModal: false,
      endDateModal: false,
      stock: "",
      headers: [
        {
          text: "Артикул",
          value: "product.title",
          sortable: false
        },
        {
          text: "Категория",
          value: "category.title",
          sortable: false
        },

        {
          text: "Цена розничная",
          value: "product.price_retail",
          sortable: false
        },
        {
          text: "Остаток на начало",
          value: "startBalance",
          sortable: false
        },
        {
          text: "Приход",
          value: "totalIncome",
          sortable: false
        },
        {
          text: "Расход",
          value: "totalOutcome",
          sortable: false
        },
        {
          text: "Остаток на конец",
          value: "endBalance",
          sortable: false
        },
        {
          text: "Действия",
          align: "right",
          sortable: false
        }
      ]
    };
  }
};
</script>
