import * as mutationTypes from './mutation-types';

export default {
    [mutationTypes.CATEGORIES_SET](state, categories) {
        state.contability.categories = categories;
    },
    [mutationTypes.CATEGORY_CREATED](state, category) {
        state.contability.categories.push(category);
    },
    [mutationTypes.CATEGORY_DELETED](state, id) {
        state.contability.categories = state.contability.categories.filter(category => category._id !== id)
    }
}