import Vue from "vue";
import Router from "vue-router";

import SettingsLayout from "./layouts/SettingsLayout.vue";

import ViewCategories from "./views/settings/categories/ViewCategories.vue";
import ViewStocks from "./views/settings/stocks/ViewStocks.vue";
import ViewProducts from "./views/settings/products/ViewProducts.vue";

import ViewWaybills from "./views/waybills/ViewWaybills.vue";

import ViewBalance from "./views/balance/ViewBalance.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/settings",
      component: SettingsLayout,
      children: [
        { path: "", redirect: "categories" },
        { path: "categories", component: ViewCategories },
        { path: "stocks", component: ViewStocks },
        { path: "products", component: ViewProducts }
      ]
    },
    {
      path: "/balances",
      component: ViewBalance
    },
    {
      path: "/waybills",
      component: ViewWaybills
    }
  ]
});
