import * as mutationTypes from "./mutation-types";

export default {
  [mutationTypes.STOCKS_SET](state, stocks) {
    state.contability.stocks = stocks;
  },
  [mutationTypes.STOCK_CREATED](state, stock) {
    state.contability.stocks.push(stock);
  },
  [mutationTypes.STOCK_DELETED](state, id) {
    state.contability.stocks = state.contability.stocks.filter(stock => stock._id !== id)
  }
};
