import * as mutationTypes from "./mutation-types";

export default {
  [mutationTypes.PRODUCTS_SET](state, products) {
    state.contability.products = products;
  },
  [mutationTypes.PRODUCT_CREATED](state, product) {
    state.contability.products.push(product);
  },
  [mutationTypes.PRODUCT_DELETED](state, id) {
    state.contability.products = state.contability.products.filter(
      product => product._id !== id
    );
  }
};
