import api from "@/plugins/api";
import * as actionTypes from "./action-types";
import * as mutationTypes from "./mutation-types";

export default {
  async [actionTypes.STOCKS_GET]({ commit }) {
    const response = await api.get("/api/settings/stocks");
    commit(mutationTypes.STOCKS_SET, response.data);
  },
  async [actionTypes.STOCK_CREATE]({ commit }, stock) {
    const response = await api.post('/api/settings/stocks', stock);
    commit(mutationTypes.STOCK_CREATED, response.data);
  },
  async [actionTypes.STOCK_UPDATE]({ commit }, stock) {
    await api.put(`/api/settings/stocks/${stock._id}`, stock)
  },
  async [actionTypes.STOCK_DELETE]({ commit }, id) {
    await api.delete(`/api/settings/stocks/${id}`);
    commit(mutationTypes.STOCK_DELETED, id);
  }

};
