import api from '@/plugins/api';
import * as actionTypes from './action-types';
import * as mutationTypes from './mutation-types';

export default {
    async [actionTypes.CATEGORIES_GET]({ commit }) {
        const response = await api.get('/api/settings/categories');
        commit(mutationTypes.CATEGORIES_SET, response.data);
    },
    async [actionTypes.CATEGORY_CREATE]({ commit }, category) {
        const response = await api.post('/api/settings/categories', category);
        commit(mutationTypes.CATEGORY_CREATED, response.data);
    },
    async [actionTypes.CATEGORY_UPDATE]({ commit }, category) {
        await api.put(`/api/settings/categories/${category._id}`, category);
    },
    async [actionTypes.CATEGORY_DELETE]({ commit }, id) {
        await api.delete(`/api/settings/categories/${id}`);
        commit(mutationTypes.CATEGORY_DELETED, id);
    }
}