import api from "@/plugins/api";

import * as actionTypes from "./action-types";
import * as mutationTypes from "./mutation-types";

const wait = ms => new Promise(resolve => setTimeout(resolve, ms));

export default {
  async [actionTypes.BALANCE_FETCH]({ commit }, options) {
    commit(mutationTypes.BALANCE_TABLE_LOADING, true);
    let response = await api.get("/api/transactions", {
      params: {
        stock_id: options.stock,
        startDate: options.startDate,
        endDate: options.endDate
      }
    });
    commit(mutationTypes.BALANCE_FETCHED, response);
    commit(mutationTypes.BALANCE_TABLE_LOADING, false);
  },

  async [actionTypes.WAYBILL_CREATE]({ commit }, waybill) {
    let response = await api.post("/api/transactions/waybill", waybill);
  },

  async [actionTypes.WAYBILL_FETCH]({ commit, params }) {
    let response = await api.get("/api/transactions/waybill");
    commit(mutationTypes.WAYBILLS_FETCHED, response.data);
    console.log(response);
  }
};
