import categoryActions from "./categories/actions";
import categoryMutations from "./categories/mutations";

import stockActions from "./stocks/actions";
import stockMutations from "./stocks/mutations";

import productActions from "./products/actions";
import productMutations from "./products/mutations";

export default {
  state: {
    contability: {
      categories: [],
      stocks: [],
      products: []
    }
  },
  actions: {
    ...categoryActions,
    ...stockActions,
    ...productActions
  },
  mutations: {
    ...categoryMutations,
    ...stockMutations,
    ...productMutations
  }
};
