import * as actionTypes from "./action-types";

import { CATEGORIES_GET } from "./settings/categories/action-types";
import { PRODUCTS_GET } from "./settings/products/action-types";
import { STOCKS_GET } from "./settings/stocks/action-types";

export default {
  [actionTypes.APP_INIT]({ dispatch }) {
    dispatch(CATEGORIES_GET);
    dispatch(PRODUCTS_GET);
    dispatch(STOCKS_GET);
  }
};
