import api from "@/plugins/api";
import * as actionTypes from "./action-types";
import * as mutationTypes from "./mutation-types";

export default {
  async [actionTypes.PRODUCTS_GET]({ commit }) {
    const response = await api.get("/api/settings/products");
    commit(mutationTypes.PRODUCTS_SET, response.data);
  },
  async [actionTypes.PRODUCT_CREATE]({ commit }, stock) {
    const response = await api.post("/api/settings/products", stock);
    commit(mutationTypes.PRODUCT_CREATED, response.data);
  },
  async [actionTypes.PRODUCT_UPDATE]({ commit }, stock) {
    await api.put(`/api/settings/products/${stock._id}`, stock);
  },
  async [actionTypes.PRODUCT_DELETE]({ commit }, id) {
    await api.delete(`/api/settings/products/${id}`);
    commit(mutationTypes.PRODUCT_DELETED, id);
  }
};
