<template>
  <v-container>
    <v-dialog v-model="dialog" persistent max-width="500">
      <v-card>
        <v-toolbar dark flat color="primary">
          <v-toolbar-title>{{this.dialogData && this.dialogData._id ? 'Редактировать' : 'Создать'}}</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-btn icon @click="closeDialog()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field v-if="dialogData._id" disabled label="UUID" v-model="dialogData._id" />
              </v-col>
              <v-col cols="12">
                <v-autocomplete
                  item-text="title"
                  item-value="_id"
                  label="Категория"
                  v-model="dialogData._category"
                  :items="categories"
                ></v-autocomplete>
              </v-col>
              <v-col cols="12">
                <v-text-field label="Артикул" v-model="dialogData.title" />
              </v-col>
              <v-col cols="6">
                <v-text-field label="Цена оптовая" v-model="dialogData.price_wholesale" />
              </v-col>
              <v-col cols="6">
                <v-text-field label="Цена розничная" v-model="dialogData.price_retail" />
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="green darken-1" text @click="closeDialog('save')">Сохранить</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-row justify="center">
      <v-col>
        <v-card>
          <v-toolbar elevation="2">
            <v-row align-content="center" justify="center">
              <v-col align-self="center">
                <v-text-field
                  placeholder="Поиск"
                  v-model="search"
                  hide-details
                  single-line
                  flat
                  solo
                  prepend-icon="mdi-magnify"
                />
              </v-col>
              <v-col md="auto" align-self="center">
                <v-btn color="blue" dark @click="openDialog({})">Добавить</v-btn>
                <v-btn class="ml-2" icon>
                  <v-icon>mdi-refresh</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-toolbar>
          <v-data-table
            :items-per-page="10"
            :items="products"
            :search="search"
            :headers="headers"
          >
            <template v-slot:item.action="{item}">
              <v-icon small class="mr-2" @click="openDialog(item)">mdi-pencil</v-icon>
              <v-icon small @click="deleteItem(item._id)">mdi-delete</v-icon>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {
  PRODUCT_CREATE,
  PRODUCT_UPDATE,
  PRODUCT_DELETE
} from "@/store/settings/products/action-types";
export default {
  computed: {
    products() {
      return this.$store.state.settings.contability.products;
    },
    categories() {
      return this.$store.state.settings.contability.categories;
    }
  },

  methods: {
    openDialog(data) {
      this.dialog = true;
      this.dialogData = Object.assign({}, data);
    },
    closeDialog(action) {
      if (action == "save") {
        if (this.dialogData._id)
          this.$store.dispatch(PRODUCT_UPDATE, this.dialogData);
        else this.$store.dispatch(PRODUCT_CREATE, this.dialogData);
      }
      this.dialog = false;
      this.dialogData = Object.assign({});
    },
    deleteItem(id) {
      this.$store.dispatch(PRODUCT_DELETE, id);
    }
  },

  data() {
    return {
      search: "",
      dialog: false,
      dialogData: {},
      headers: [
        { text: "ID", align: "left", sortable: false, value: "_id" },
        {
          text: "Категория",
          align: "left",
          sortable: true,
          value: "_category.title"
        },
        {
          text: "Артикул",
          align: "left",
          sortable: true,
          value: "title"
        },
        {
          text: "Стоимость",
          align: "left",
          sortable: false,
          value: "price_retail"
        },
        { text: "Создано", align: "left", sortable: false, value: "createdAt" },
        {
          text: "Обновлено",
          align: "left",
          sortable: false,
          value: "updatedAt"
        },
        {
          text: "Действия",
          align: "right",
          sortable: false,
          value: "action"
        }
      ]
    };
  }
};
</script>

