<template>
  <v-dialog persistent max-width="600px" v-model="opened">
    <template v-slot:activator="{on}">
      <v-btn
        small
        depressed
        dark
        tile
        color="green"
        class="mt-2 mb-5"
        v-on="on"
        :disabled="disabled"
      >Добавить позицию</v-btn>
    </template>
    <v-card>
      <v-toolbar dark flat color="green">
        <v-container>
          <v-row align="center">
            <v-col>Добавить позицию</v-col>
          </v-row>
        </v-container>
      </v-toolbar>
      <v-card-text>
        <v-container>
          <v-form ref="form" v-model="formValid">
            <v-row align="center">
              <v-col cols="6">
                <v-autocomplete
                  v-model="product"
                  :items="products"
                  :rules="modelRules"
                  label="Выбрать"
                  item-text="title"
                  return-object
                />
              </v-col>
              <v-col cols="6">
                <v-text-field
                  :rules="countRules"
                  v-model="quantity"
                  type="number"
                  label="Количество"
                  :items="products"
                />
              </v-col>
            </v-row>
            <v-row v-if="product._id && action === 'sell'">
              <v-col cols="9">
                <v-radio-group label="Тип цены" row v-model="price.type">
                  <v-radio label="Розничная" :value="'retail'" />
                  <v-radio label="Оптовая" :value="'wholesale'" />
                </v-radio-group>
              </v-col>
              <v-col cols="3">
                <v-text-field :value="price.value" disabled label="Цена" />
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <div class="flex-grow-1" />
        <v-btn text @click="closeDialog">Отмена</v-btn>
        <v-btn :disabled="!formValid" text color="green" @click="addItem">Добавить</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    disabled: Boolean,
    action: String
  },
  computed: {
    products() {
      return this.$store.state.settings.contability.products;
    }
  },
  methods: {
    addItem() {
      this.$emit("onAddItem", {
        product: Object.assign({}, this.product),
        price: Object.assign({}, this.price),
        quantity: this.quantity
      });
      this.closeDialog();
    },
    closeDialog() {
      this.$refs.form.resetValidation();

      this.product = {
        _id: "",
        title: ""
      };
      this.price = {
        value: 0,
        type: "retail"
      };
      this.quantity = 0;
      this.opened = false;
    }
  },
  watch: {
    product(newVal, oldVal) {
      if (this.price.type === "retail") this.price.value = newVal.price_retail;
      else this.price.value = newVal.price_wholesale;
    },
    price: {
      handler(newVal, oldVal) {
        if (this.price.type === "retail")
          this.price.value = this.product.price_retail;
        else this.price.value = this.product.price_wholesale;
      },
      deep: true
    }
  },
  data() {
    return {
      opened: false,
      formValid: false,
      modelRules: [v => !!v || "Выберите артикул"],
      countRules: [v => /^[1-9][0-9]*$/.test(v) || "Положительное целое число"],
      product: {
        _id: "",
        title: ""
      },
      quantity: 0,
      price: {
        value: 0,
        type: "retail"
      }
    };
  }
};
</script>

