<template>
  <div>
    <v-card color="blue" tile flat>
      <v-card-text>
        <v-card-title class="white--text">Накладные</v-card-title>
      </v-card-text>
    </v-card>
    <v-container>
      <v-row justify="center">
        <v-col>
          <v-toolbar elevation="2">
            <v-row align="center">
              <v-col>
                <v-text-field
                  label="Поиск накладной"
                  hide-details
                  flat
                  solo
                  prepend-icon="mdi-magnify"
                />
              </v-col>
              <v-col cols="auto">
                <CreateWaybill />
              </v-col>
            </v-row>
          </v-toolbar>
          <v-data-iterator :items="waybills">
            <template v-slot:default="props">
              <v-row>
                <v-col cols="12" :key="item.waybill_id" v-for="item in props.items">
                  <Waybill :details="item" />
                </v-col>
              </v-row>
            </template>
          </v-data-iterator>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import moment from "moment";
import { WAYBILL_FETCH } from "@/store/transaction/action-types";
import CreateWaybill from "./CreateWaybill";
import Waybill from "./components/WaybillCard";

export default {
  components: {
    CreateWaybill,
    Waybill
  },
  computed: {
    waybills() {
      return this.$store.state.transaction.waybills || [];
    }
  },
  mounted() {
    this.$store.dispatch(WAYBILL_FETCH);
  },

  methods: {},
  data() {
    return {};
  }
};
</script>
