<template>
  <v-dialog max-width="1100px" persistent v-model="opened">
    <template v-slot:activator="{on}">
      <v-btn color="primary" text v-on="on">Создать</v-btn>
    </template>
    <v-card>
      <v-toolbar dark flat color="primary">
        <v-container>
          <v-row align="center">
            <v-col>Накладная</v-col>
            <v-col cols="auto">{{_moment().locale('ru').format('DD MMMM, YYYY')}}</v-col>
          </v-row>
        </v-container>
      </v-toolbar>
      <v-card-text>
        <v-container>
          <v-form ref="form" v-model="formValid">
            <v-row align="center">
              <v-col cols="4">
                <v-select
                  v-model="waybill.action"
                  :items="wbActions"
                  :rules="requiredRules"
                  item-value="type"
                  item-text="title"
                  label="Тип накладной"
                  return-object
                ></v-select>
              </v-col>
              <v-col cols="4" v-if="waybill.action.source === true">
                <v-autocomplete
                  v-model="waybill.source"
                  :items="stocks"
                  :rules="requiredRules"
                  autocomplete="off"
                  label="Расход из"
                  item-value="_id"
                  item-text="title"
                />
              </v-col>
              <v-col cols="4" v-if="waybill.action.destination === true">
                <v-autocomplete
                  v-model="waybill.destination"
                  :items="stocks"
                  :rules="requiredRules"
                  autocomplete="off"
                  label="Приход в"
                  item-value="_id"
                  item-text="title"
                />
              </v-col>
            </v-row>
          </v-form>
          <v-row>
            <v-col>
              <AddWaybillItem :action="waybill.action.type" :disabled="!formValid" @onAddItem="addWaybillItem" />
              <v-data-table
                v-if="formValid"
                height="350px"
                fixed-header
                :items-per-page="5"
                :headers="headers"
                :items="waybill.products"
              >
                <template v-slot:body="{items}">
                  <tbody>
                    <tr v-for="item in items" :key="item._id">
                      <td>{{item.product._category.title}}</td>
                      <td>{{item.product.title}}</td>
                      <td>{{item.price.value}} {{item.price.type}}</td>
                      <td>{{item.quantity}} {{item.product._category.unit}}</td>
                      <td>{{item.price.value * item.quantity}}</td>
                    </tr>
                    <tr>
                      <td colspan="4">
                        <strong>Итого</strong>
                      </td>
                      <td>
                        <strong>{{ totalSumm }}</strong>
                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-data-table>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <div class="flex-grow-1" />
        <v-btn text @click="closeWaybillDialog">Отмена</v-btn>
        <v-btn :disabled="!hasItems" text color="green" @click.stop="createWaybill">Сохранить</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import moment from "moment";
import api from "@/plugins/api";

import wbActions from "./waybillActions";

import AddWaybillItem from "./AddWaybillItem.vue";
import { WAYBILL_CREATE } from "@/store/transaction/action-types";

export default {
  components: {
    AddWaybillItem
  },
  computed: {
    stocks() {
      return this.$store.state.settings.contability.stocks;
    },
    totalSumm() {
      return this.waybill.products.reduce(
        (acc, item) => acc + item.price.value * item.quantity,
        0
      );
    },
    _moment() {
      return () => moment();
    },
    hasItems() {
      return this.waybill.products.length > 0;
    }
  },
  methods: {
    addWaybillItem(item) {
      this.waybill.products.push(item);
    },
    async createWaybill() {
      let waybill = Object.assign(
        {},
        {
          action: this.waybill.action,
          source: this.waybill.source,
          destination: this.waybill.destination,
          products: this.waybill.products.map(item => ({
            _id: item.product._id,
            title: item.product.title,
            category: item.product._category.title,
            unit: item.product._category.unit,
            price: item.price,
            quantity: item.quantity
          })),
          date: moment().format("YYYY-MM-DD")
        }
      );
      this.$store.dispatch(WAYBILL_CREATE, waybill);
      this.closeWaybillDialog();
    },
    closeWaybillDialog() {
      this.$refs.form.reset();
      this.opened = false;
      this.waybill = {
        action: "",
        source: "",
        destination: "",
        products: []
      };
    }
  },
  data() {
    return {
      wbActions: wbActions,
      opened: false,
      loading: false,
      formValid: false,
      requiredRules: [v => !!v || "Это поле необходимо"],
      waybill: {
        action: "",
        source: "",
        destination: "",
        products: []
      },
      headers: [
        {
          text: "Категория",
          value: "product._category.title"
        },
        {
          text: "Номенклатура",
          value: "product.title"
        },
        {
          text: "Цена",
          value: "product.price_retail"
        },
        {
          text: "Количество",
          value: "quantity"
        },
        {
          text: "Сумма",
          sortable: false
        }
      ]
    };
  }
};
</script>